.mobile-app {
  font-family: "Open Sans", "Arial", sans-serif;
  color: white;

  background-color: #1d2631;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding: 0;

    background:
      url("../img/iphone-hand-tablet@1x.png") no-repeat 47% bottom,
      url("../img/bg-back-tablet@1x.jpg") no-repeat center center;
    background-color: #1d2631;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background:
        url("../img/iphone-hand-tablet@2x.png") no-repeat 47% bottom,
        url("../img/bg-back-tablet@2x.jpg") no-repeat center 0;
      background-size: 697px 545px, auto 739px;
    }
  }

  @media (min-width: @desktop-width) {
    min-height: 853px;

    background:
      url("../img/iphone-hand-desktop@1x.png") no-repeat right bottom,
      url("../img/bg-back-desktop@1x.jpg") no-repeat center center;

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background:
        url("../img/iphone-hand-desktop@2x.png") no-repeat right bottom,
        url("../img/bg-back-desktop@2x.jpg") no-repeat center center;
      background-size: 1056px 644px, auto 854px;
    }
  }
}

.mobile-app__wrapper {
  text-align: center;

  @media (min-width: @tablet-width) {
    margin-left: 3.33%;
    min-height: 619px;
    width: 45%;
  }

  @media (min-width: @desktop-width) {
    width: 25%;
    margin-left: 10.8%;
  }
}

.mobile-app__title {
  min-height: 342px;

  background:
    url("../img/iphone-hand-mobile@1x.png") no-repeat left bottom,
    url("../img/bg-back-mobile@1x.jpg") no-repeat center center;
  background-color: #1d2631;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/iphone-hand-mobile@2x.png"), url("../img/bg-back-mobile@2x.jpg");
  }

  background-size:
    258px 286px,
    100% 100%;

  .indent-reset ();
  margin: 0 auto;
  padding: 86px 14% 33px 14%;

  font-size: 24px;
  font-weight: 300;
  color: white;
  text-align: center;

  @media (min-width: @tablet-width) {
    min-height: auto;
    padding-top: 203px;
    padding-bottom: 8px;
    line-height: 1.17;

    background: none;

    font-size: 40px;
  }

  @media (min-width: @desktop-width) {
    padding: 219px 0 29px;

    background: none;

    font-size: 50px;
  }
}

.mobile-app__button {
  margin: 28px auto 35px;
  padding: 11px 37px 14px 36px;

  display: inline-block;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: white;

  border: 3px solid white;
  border-radius: 33px;

  &:hover,
  &:focus {
    color: #d22856;
    background-color: white;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    padding: 11px 45px 14px 43px;
    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 70px;
    padding: 9px 29px 12px 28px;

    font-size: 21px;
  }
}

.mobile-app__list {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 64%;

  padding: 0;
  margin: 0 auto 17px;

  @media (min-width: @tablet-width) {
    width: 68%;
    margin-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 48px;
  }
}

.mobile-app__item {
  &:first-of-type {
    & svg {
      margin-top: -3px;
    }
  }

  &:nth-of-type(2) {
    & svg {
      margin-top: 3px;
    }
  }

  &:last-of-type {
    & svg {
      margin-top: 4px;
    }
  }
}

.mobile-app__available {
  .indent-reset ();
  margin-right: 30px;
  margin-left: 30px;
  padding-bottom: 32px;

  .font (14px, 300, 1.71);
  text-align: center;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding-bottom: 100px;
    font-size: 18px;
  }
}
